export interface menuItem {
  name: string;
  displayName?: string;
  icon?: string;
  iconActive?: string;
  url?: string;
  image?: string;
  active?: boolean;
  title?: string;
  subTitle?: string;
  index?: number;
  childMenuLinks?: string[];
  subMenuItems?: menuItem[];
  defaultChildMenu?: string;
  childMenu?: menuItem[];
  parent?: string;
  automotiveService?: string | string[];
  permission?: boolean;
  color?:string;
  permissionName?: string;
  disabled?: boolean;
}

export interface partialUrlsMatchItem {
  partialUrl: string;
  title?: string;
  subTitle?: string;
  icon?: string;
  currentParent?: string;
  currentChild?: string;
}

