import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, CanActivate, Router, RouterStateSnapshot, UrlTree } from '@angular/router';
import { MsalGuard } from '@azure/msal-angular';
import { catchError, map, Observable, of } from 'rxjs';
import { switchMap } from 'rxjs/operators';
import { CommonService } from 'src/app/services/common/common.service';
import { CoreService } from 'src/app/services/core/core.service';

@Injectable({
  providedIn: 'root',
})
export class RepairGuard implements CanActivate {
  /**
   * constructor
   * @param router
   */
  constructor(
    private commonService: CommonService,
    private msalGuard: MsalGuard,
    private router: Router,
    private coreService: CoreService
  ) { }

  /**
   * canActivate
   * @returns
   */
  canActivate(next: ActivatedRouteSnapshot, state: RouterStateSnapshot):
    | Observable<boolean | UrlTree>
    | Promise<boolean | UrlTree>
    | boolean
    | UrlTree {
    if (this.msalGuard.canActivate(next, state)) {
      return this.checkUserPermission(next);
    } else {
      this.router.navigate([ 'home' ]);
      return false;
    }
  }

  /**
  * checkUserPermission
  * @returns
  */
  checkUserPermission(route: ActivatedRouteSnapshot): Observable<boolean> {
    return this.commonService.userProfileData.pipe(
      switchMap((res: any) => {
        if (res) {
          const automotiveServices = res?.data?.userPermission?.automotiveServices;
          this.commonService.setPermission(automotiveServices);
          const automotiveService = automotiveServices?.find(x => x.automotiveServiceName.toLowerCase() === 'addenda repair');
          if(automotiveService) {
            if(automotiveService.roleName.toLowerCase() === 'bodyshop manager') {
              return of (true)
            } else if(automotiveService.roleName.toLowerCase() === 'service desk') {
              this.router.navigate([ 'repair/lounge' ]);
              return of (false)
            } else if(automotiveService.roleName.toLowerCase() === 'driver') {
              this.router.navigate([ 'repair/outbound' ]);
              return of (false)
            } else {
              return of (true)
            }
          } else {
            this.router.navigate([ 'home' ]);
            return of (false)
          } 

        } else {
          this.commonService.showLoading();
          return this.coreService.getUserProfile().pipe(
            switchMap((res) => {
              if (res) {
                this.commonService.hideLoading();
                this.commonService.userProfileData.next(res);
                if (res?.data.userProfileImageBlobUrl) {
                  this.commonService.avatar.next({
                    image: res?.data.userProfileImageBlobUrl
                  });
                }
                if (res?.data?.userPermission?.automotiveServices) {
                  const automotiveServices = res?.data?.userPermission?.automotiveServices;
                  this.commonService.setPermission(automotiveServices);
                  const automotiveService = automotiveServices?.find(x => x.automotiveServiceName.toLowerCase() === 'addenda repair');
                  if(automotiveService) {
                    if(automotiveService.roleName.toLowerCase() === 'bodyshop manager') {
                      return of (true)
                    } else if(automotiveService.roleName.toLowerCase() === 'service desk') {
                      this.router.navigate([ 'repair/lounge' ]);
                      return of (false)
                    } else if(automotiveService.roleName.toLowerCase() === 'driver') {
                      console.log('driver2')
                      if(this.router.url.indexOf('/outbound') !== -1){
                        this.router.navigate([ 'repair/outbound' ]);
                        return of (false)
                      }
                      return of (true)
                    } else {
                      return of (true)
                    }
                  } else {
                    this.router.navigate([ 'home' ]);
                    return of (false)
                  } 
                } else {                  
                  this.router.navigate([ 'home' ]);
                  return of(true);
                }
              } else {
                this.commonService.hideLoading();
                this.router.navigate([ 'home' ]);
                return of(false);
              }
            })
          )
        }
      })
    );
  }
}
