import { environment } from 'src/environments/environment';
import { Injectable } from '@angular/core';
import * as signalR from '@microsoft/signalr'
import { BehaviorSubject, lastValueFrom } from 'rxjs';
import { CommonService } from '../common/common.service';

@Injectable({
  providedIn: 'root'
})
export class SignalrService {
  private hubConnection!: signalR.HubConnection;
  hubRecievedMessage: BehaviorSubject<number>;
  userNotificationKey = ''
  /**
   * constructor
   */
  constructor(private commonService:CommonService) {
    this.hubRecievedMessage = new BehaviorSubject<number>(null);
    if(this.commonService.repairPermission) {
      this.commonService.userProfileData.subscribe((data)=> {
        const automotiveServiceId = data.data.roleDetail.find(el => el.automotiveServiceName.toLowerCase() === 'addenda repair')?.automotiveServiceId
        this.userNotificationKey = `addendauser_${automotiveServiceId}_${data.data.id}`
        this.initiateSignalrConnection();
      })
    }
  }

  /**
   * subscribeToNotification
   */
  private subscribeToNotification(): void {
    this.hubConnection.invoke(environment.signalr.suscribeMethodName, this.userNotificationKey)
  }

  /**
   * setSignalrClientMethods
   */
  private initiateSignalrConnection(): Promise<boolean>{
    return new Promise((resolve, reject) => {
      this.hubConnection = new signalR.HubConnectionBuilder()
        .configureLogging(signalR.LogLevel.Debug)
        .withUrl(environment.signalr.notifyUrl, {
          // eslint-disable-next-line @typescript-eslint/naming-convention
          headers: { 'Ocp-Apim-Subscription-Key': environment.subscriptionKey },
          accessTokenFactory: () => {
            return lastValueFrom(this.commonService.getAccessToken())
          },
        }) // the SignalR server url
        .withAutomaticReconnect()
        .build();

      this.setSignalrClientMethods();
      this.hubConnection
        .start()
        .then(() => {
          this.subscribeToNotification();
          resolve(true);
        })
        .catch((error) => {
          console.log('Websocket Error:', error)
          reject(false);
        });
    });
  }


  /**
   * setSignalrClientMethods
   */
  private setSignalrClientMethods(): void {
    this.hubConnection.on(environment.signalr.listenerMethodNameForUser, (data: any) => {
      this.hubRecievedMessage.next(data.message);
    });
  }


  /**
   * stopSignalrConnection
   */
  public stopSignalrConnection(): void {
    this.hubConnection?.stop()
      .then(()=> {
        // then block
      })
      .catch((error)=> {
        // error
      })
  }
}
