import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, CanActivate, Router, RouterStateSnapshot, UrlTree } from '@angular/router';
import { MsalGuard, MsalService } from '@azure/msal-angular';
import { SilentRequest } from '@azure/msal-browser';
import { AuthenticationResult } from '@azure/msal-common';
import { Observable, of } from 'rxjs';
import { switchMap } from 'rxjs/operators';
import { CommonService } from 'src/app/services/common/common.service';
import { CoreService } from 'src/app/services/core/core.service';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root',
})
export class HomeGuard implements CanActivate {
  /**
   * constructor
   * @param router
   */
  constructor(
    private commonService: CommonService,
    private msalGuard: MsalGuard,
    private router: Router,
    private coreService:CoreService,
    private msalService: MsalService
  ) { }

  /**
   * canActivate
   * @returns
   */
  canActivate(next: ActivatedRouteSnapshot, state: RouterStateSnapshot):
    | Observable<boolean | UrlTree>
    | Promise<boolean | UrlTree>
    | boolean
    | UrlTree {
    if (this.msalGuard.canActivate(next, state)) {
      return this.checkUserPermission(next);
    } else {
      return false;
    }
  }

  /**
  * checkUserPermission
  * @returns
  */
  checkUserPermission(route: ActivatedRouteSnapshot): Observable<boolean> {
    return this.commonService.userProfileData.pipe(
      switchMap((res: any) => {
        if(res) {
          const automotiveServices = res?.data?.userPermission?.automotiveServices;
          this.commonService.setPermission(automotiveServices);
          // eslint-disable-next-line max-len
          const repair = automotiveServices?.find(x => x.automotiveServiceName.toLowerCase() === 'addenda repair');
          const quote = automotiveServices?.find(x => x.automotiveServiceName.toLowerCase() === 'carheal quote');
          const claim = automotiveServices?.find(x => x.automotiveServiceName.toLowerCase() === 'addenda claims');
          if(repair) {
            if(repair.roleName.toLowerCase() === 'bodyshop manager') {
              return of (true)
            } else if(repair.roleName.toLowerCase() === 'service desk') {
              this.router.navigate([ 'repair/lounge' ]);
              return of (false)
            } else if(repair.roleName.toLowerCase() === 'driver') {
              this.router.navigate([ 'repair/outbound' ]);
              return of (false)
            } else {
              this.router.navigate([ 'repair' ]);
              return of (false)
            }
          } else if(quote) {
            this.router.navigate([ 'quote' ]);
            return of (false)
          } else if(claim) {

            // if(automotiveService.roleName.toLowerCase() === 'claim manager') {
            //   return of (true);
            // } else {
            //   this.router.navigate([ 'claim/my-claim' ]);
            //   //return of (false);
            //   return of (true);
            // }
            this.router.navigate([ 'claim' ]);
            return of (false);
          } else {
            const request = {
              redirectStartPage: '/',
              scopes: [ 'openid', 'profile', `${environment.b2cSettings.caseManagementAppClientId}` ]
            };        
            const activeAccount = this.msalService.instance.getActiveAccount();
            this.msalService.acquireTokenSilent(request as SilentRequest).subscribe({
              next: (result: AuthenticationResult) => {
                this.commonService.clearData();
                this.msalService.logoutRedirect(
                  {
                    account: activeAccount,
                    idTokenHint: result.idToken
                  });          
              }
            });                    
            return of (false)
          }
        } else {
          this.commonService.showLoading();
          return this.coreService.getUserProfile().pipe(
            switchMap((res)=> {
              if(res) {
                this.commonService.hideLoading();                      
                this.commonService.userProfileData.next(res);
                if (res?.data.userProfileImageBlobUrl) { 
                  this.commonService.avatar.next({
                    image: res?.data.userProfileImageBlobUrl
                  });
                }
                if(res?.data?.userPermission?.automotiveServices) {                     
                  const automotiveServices = res?.data?.userPermission?.automotiveServices;       
                  this.commonService.setPermission(automotiveServices);
                  const automotiveService = automotiveServices?.find(x => x.automotiveServiceName.toLowerCase() === 'addenda repair');
                  if(automotiveService) {
                    if(automotiveService.roleName.toLowerCase() === 'bodyshop manager') {
                      return of (true)
                    } else if(automotiveService.roleName.toLowerCase() === 'service desk') {
                      this.router.navigate([ 'repair/lounge' ]);
                      return of (false)
                    } else if(automotiveService.roleName.toLowerCase() === 'driver') {
                      this.router.navigate([ 'repair/outbound' ]);
                      return of (false)
                    } else {
                      this.router.navigate([ 'repair' ]);
                      return of (false)
                    }
                  } else {
                    this.router.navigate([ 'quote' ]);
                    return of (false);
                  } 
                } else {                  
                  return of(true);
                }
              } else {
                this.commonService.hideLoading();
                return of(true);
              }
            })     
          )
        }
      })
    );
  }
}
