interface actions {
  title?: string;
  icon?: string;
  prop?: string;
  action: (params: any) => void
}

interface keyValue {
  displayValue: string;
  keyValue: string;
}

export interface SearchSectionModel {
  title: string;
  icon: string;
  keyValue: keyValue[];
  data: any[],
  count: number,
  actions?: actions[],
  searchKey?: string
}

export interface SearchFilterParams {
  searchKey: string;
  sectionName?: string;
  pageSize: number;
  pageNumber: number
}

