  /**
   * return access token
   */
  getAccessToken(): Observable<string> {
    const request = {
      redirectStartPage: '/',
      scopes: [ 'openid', 'profile', `${environment.b2cSettings.caseManagementAppClientId}` ]
    };
    return this.msalService.acquireTokenSilent(request).pipe(map(el=> el.accessToken))
  }

