  /**
   * check image orientation
   */
  checkImageOrientation(image): Promise<any> {
    return new Promise((resolve) => {
      const img = new Image();
      img.src = image;
      img.onload = (): any => {
        if (img.height > img.width) {
          resolve(true);
        } else {
          resolve(false);
        }
      }

      img.onerror = (): any => {
        resolve(false);
      }
    })
  }

